.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  transition: background-color 1s;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.box {
  width: 60vw;
  height: auto;
  border-radius: 15px;
  background-color: white;
  color: #282c34;
  transition: color 1s;
  display: grid;
}

.text-box {
  width: 100%;
  height: auto;
}

.button-box {
  width: 100%;
  margin: 40px 0;
  display: flex;
}

.twitter-button {
  width: 50%;
  float: left;
  align-self: center;
}

a:hover, button:hover {
  opacity: 0.8;
}

.icon {
  width: 50px;
  height: auto;
  padding: 10px 30px;
  border-radius: 15px;
  background-color: #282c34;
  transition: background-color 1s;
}

.quote-button {
  width: 50%;
  float: right;
  align-self: center;
}

.new-quote {
  padding: 0;
  border-radius: 15px;
  border: none;
  cursor: pointer;
}

.text, .author {
  margin: 40px 50px;
}

.author {
  text-align: right;
  font-weight: 300;
}

.author::before {
  content: '- ';
}

.text::before, .text::after {
  content: '"';
}